<template>
  <v-row class="app-invoice-preview">
    <v-col
      cols="12"
      md="9"
    >
      <quota-editable :data="quotaData" />
    </v-col>

    <!-- Right Column: Quota Action -->
    <v-col
      cols="12"
      md="3"
    >
      <v-card class="mb-6">
        <v-card-text>
          <v-btn
            color="primary"
            class="mb-3"
            block
            @click="isQuotaSendSidebarActive = true"
          >
            <v-icon
              class="me-2"
              left
            >
              {{ icons.mdiSendOutline }}
            </v-icon>
            <span>Send Quota</span>
          </v-btn>
          <v-btn
            class="mb-3"
            color="secondary"
            block
            outlined
          >
            Preview
          </v-btn>
          <v-btn
            class="mb-3"
            color="secondary"
            block
            outlined
          >
            Save
          </v-btn>
          <v-btn
            color="success"
            block
            @click="isAddPaymentSidebarActive = true"
          >
            <v-icon
              class="me-2"
              left
            >
              {{ icons.mdiCurrencyUsd }}
            </v-icon>
            <span>Add Payment</span>
          </v-btn>
        </v-card-text>
      </v-card>

      <v-select
        v-model="quotaData.paymentMethod"
        outlined
        :items="paymentMethods"
        dense
        placeholder="Accept Payment Via"
        :menu-props="{ offsetY: true }"
        hide-details="auto"
        class="mb-6"
      />

      <div class="d-flex align-center justify-space-between mb-3">
        <span>Payment Terms</span>
        <v-switch
          hide-details="auto"
          class="mt-0"
          :input-value="true"
        />
      </div>

      <div class="d-flex align-center justify-space-between mb-3">
        <span>Client Notes</span>
        <v-switch
          hide-details="auto"
          :input-value="false"
          class="mt-0"
        />
      </div>
      <div class="d-flex align-center justify-space-between">
        <span>Payment Stub</span>
        <v-switch
          class="mt-0"
          hide-details="auto"
          :input-value="false"
        />
      </div>
    </v-col>

    <!-- quota send drawer -->
    <quota-sidebar-send-quota v-model="isQuotaSendSidebarActive" />

    <!-- quota add payment drawer -->
    <quota-sidebar-add-payment v-model="isAddPaymentSidebarActive" />
  </v-row>
</template>

<script>
import { mdiSendOutline, mdiCurrencyUsd } from '@mdi/js';
import { ref, onUnmounted } from '@vue/composition-api';
import store from '@/store';
import router from '@/router';
import QuotaEditable from '../QuotaEditable.vue';
import quotaStoreModule from '../quotaStoreModule';
import QuotaSidebarSendQuota from '../QuotaSidebarSendQuota.vue';
import QuotaSidebarAddPayment from '../QuotaSidebarAddPayment.vue';

export default {
  components: {
    QuotaEditable,
    QuotaSidebarSendQuota,
    QuotaSidebarAddPayment,
  },
  setup() {
    const QUOTA_APP_STORE_MODULE_NAME = 'app-quota';

    const isQuotaSendSidebarActive = ref(false);
    const isAddPaymentSidebarActive = ref(false);

    // Register module
    if (!store.hasModule(QUOTA_APP_STORE_MODULE_NAME)) {
      store.registerModule(QUOTA_APP_STORE_MODULE_NAME, quotaStoreModule);
    }

    // UnRegister on leave
    onUnmounted(() => {
      if (store.hasModule(QUOTA_APP_STORE_MODULE_NAME)) store.unregisterModule(QUOTA_APP_STORE_MODULE_NAME);
    });

    const quotaData = ref({});

    store
      .dispatch('app-quota/fetchQuota', { id: router.currentRoute.params.id })
      .then((response) => {
        quotaData.value = {
          quotaData: response.data.quota,
          paymentDetails: response.data.paymentDetails,

          // ? We are adding some extra data in response for data purpose
          // * Your response will contain this extra data
          // ? [Purpose is to make it more API friendly and less static as possible]
          purchasedProducts: [
            {
              itemTitle: 'App Design',
              cost: 24,
              hours: 2,
              description: 'Designed UI kit & app pages.',
            },
          ],
          note: 'It was a pleasure working with you and your team. We hope you will keep us in mind for future freelance projects. Thank You!',
          paymentMethod: 'Bank Account',
          salesperson: 'Tom Cook',
          thanksNote: 'Thanks for your business',
        };
      })
      .catch((error) => {
        if (error.response.status === 404) {
          quotaData.value = undefined;
        }
      });

    const paymentMethods = ['Bank Account', 'PayPal', 'UPI Transfer'];

    return {
      quotaData,
      paymentMethods,

      // drawer
      isQuotaSendSidebarActive,
      isAddPaymentSidebarActive,

      // Icons
      icons: {
        mdiSendOutline,
        mdiCurrencyUsd,
      },
    };
  },
};
</script>

<style lang="scss">
@import '~@core/preset/preset/apps/invoice.scss';
</style>
